.advantages {
  background-color: $mid-grey-color;
  padding-top: 35px;
  padding-bottom: 60px;
}

.advantages__list {
  margin: 0;
  padding: 0;
  list-style: none;
}


// Label
.advantages__label {
  background-color: $gold-color;
  padding: 40px;
  transition: transform ease-in .3s;
  cursor: pointer;

  @include mobile-max {
    padding: 20px 20px 40px;
  }
}

.advantages__label:hover {
  transform: translateY(-15px);
}

.advantages__name {
  font-style: normal;
  font-weight: 500;
  font-size: 27px;
  line-height: 34px;
  color: #232124;
  text-transform: uppercase;
  letter-spacing: .03em;

  @include mobile-max {
    font-size: 14px;
    line-height: 32px;
  }
}

.advantages__desc {
  font-style: normal;
  font-weight: 300;
  font-size: 19px;
  line-height: 27px;
  color: #000000;
  width: 90%;

  @include mobile-max {
    font-size: 13px;
    line-height: 19px;
  }
}

.advantages__name,
.advantages__desc {
  margin: 0;
}

.advantages__name {
  margin-bottom: 25px;

  @include mobile-max {
    margin-bottom: 8px;
  }
}

.advantages__img-wrapper {
  @include mobile-max {
    width: 100%;
  }
}

.advantages__img-wrapper img {
  width: 100%;
  height: 100%;
  object-fit: cover;

  @include mobile-max {
    height: 262px;
  }
}

// Layout
.advantages__grid {
  --img-height: 524px;
  --img-width: 360px;
  --gap: 20px;
  display: grid;
  gap: var(--gap);
  grid-auto-flow: dense;
  grid-template-rows: repeat(auto-fit, minmax(calc(var(--img-height) * 0.5), 1fr));
  grid-template-columns: var(--img-width) repeat(2, calc((100% - var(--img-width) - 2 * var(--gap)) * 0.5));

  @include tablet-max {
    --img-width: 260px;
    grid-template-columns: var(--img-width) 1fr;
  }

  @include mobile-max {
    display: flex;
    flex-wrap: wrap;
    gap: 0;
  }
}

@include mobile-max {
  .advantages__grid>div {
    margin-bottom: 20px;
  }
}

.advantages__img-wrapper {
  grid-row: span 2;
}

.advantages__label--two-column {
  grid-column: span 2;

  @include tablet-max {
    grid-column: span 1;
  }
}

.advantages__label--two-column-tablet {
  @include tablet-max {
    grid-column: span 2;
  }
}

.advantages__img-wrapper--column-end {
  grid-column-end: -1;
}

.advantages__img-wrapper--oversize {
  grid-column: span 2;

  @include tablet-max {
    grid-row-start: 3;
  }
}

.advantages__img-wrapper--oversize+.advantages__label {
  grid-row: span 2;
}

.advantages .typography-h2 {
  margin-bottom: 30px;

  @include mobile-max {
    font-size: 20px;
    margin-bottom: 9px;
  }
}
